import { useState } from "react";
import {
  IonApp,
  IonPage,
  IonContent,
  IonList,
  IonItem,
  IonLabel,
  IonHeader,
  IonTitle,
  IonToolbar,
  IonCheckbox,
  IonFab,
  IonFabButton,
  IonIcon,
  IonItemSliding,
  IonItemOptions,
  IonItemOption,
} from "@ionic/react";
import { add } from "ionicons/icons";
import { loadTodos, addTodo, updateTodo, deleteTodo } from "./storage.js";

export default function App() {
  const [todos, setTodos] = useState(loadTodos());
  return (
    <IonApp>
      <IonPage>
        <IonHeader>
          <IonToolbar>
            <IonTitle>TODO App</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            {todos.map((item, i) => {
              return (
                <IonItemSliding key={i}>
                  <IonItem>
                    <IonLabel>{item.text}</IonLabel>
                    <IonCheckbox
                      checked={item.finished}
                      onIonChange={() => {
                        const newItem = { ...item };
                        newItem.finished = !newItem.finished;
                        setTodos(updateTodo(i, newItem));
                      }}
                    />
                  </IonItem>
                  <IonItemOptions side="end">
                    <IonItemOption
                      onClick={() => {
                        setTodos(deleteTodo(i));
                      }}
                    >
                      Delete
                    </IonItemOption>
                  </IonItemOptions>
                </IonItemSliding>
              );
            })}
          </IonList>
        </IonContent>
        <IonFab
          vertical="bottom"
          horizontal="end"
          slot="fixed"
          onClick={() => {
            const newItem = { text: prompt("TODO"), finished: false };
            setTodos(addTodo(newItem));
          }}
        >
          <IonFabButton>
            <IonIcon icon={add} />
          </IonFabButton>
        </IonFab>
      </IonPage>
    </IonApp>
  );
}
